<template>
  <v-menu
    v-model="menu2"
    :close-on-content-click="false"
    :nudge-right="40"
    transition="scale-transition"
    offset-y
    :class="ccsClass"
    min-width="200px"
  >
    <template v-slot:activator="{ on, attrs }">
      <v-text-field
        v-model="dateModel"
        :label="label"
        locale="pt"
        :prepend-inner-icon="icon"
        readonly
        :outlined="outlined"
        :dense="dense"
        v-bind="attrs"
        color="primary"
        :class="ccsClass"
        :success="success"
        v-on="on"
      >
        <template v-slot:prepend-inner>
          <v-icon
            class="material-icons-outlined"
            :class="success ? 'success--text' : ''"
            dense
          >
            event
          </v-icon>
        </template>
      </v-text-field>
    </template>
    <v-date-picker
      v-model="date"
      :min="min"
      :max="max"
      :range="range"
      @input="menu2 = false"
      color="primary"
    ></v-date-picker>
  </v-menu>
</template>

<script>
import DateMixin from '@/mixins/DateMixin'
export default {
  name: 'DatePicker',
  mixins: [ DateMixin ],
  props: {
    ccsClass: { default: '' },
    id: { default: null },
    label: { default: null },
    icon: { default: 'event' },
    success: { default: false },
    min: { default: null },
    max: { default: null },
    value: { default: null  },
    outlined: { default: null },
    dense: { default: false },
    range: { default: false }
  },
  data: () => ({
    date: null,
    menu2: false
  }),
  mounted () {
    if (this.value) this.date = this.value
    else {
      let date = new Date()
      this.date = this.formatDateToDateEn(date)
    }
  },
  computed: {
    dateModel () {
      let date = new Date(this.date)
      date.setDate(date.getDate() + 1)
      return this.formatDateToDatePtBr(date)
    }
  },
  watch: {
    value (val) {
      this.data = val
    },
    date (date) {
      this.$emit('change', date, this.id)
    }
  }
}
</script>